<template>
    <div>
        <el-row>
            <headers :title='title'></headers>
        </el-row>
        <el-row>
            <el-col :span='18' :offset='3'>
                <div class="data_list">
                    <div class="data_title">
                        <span>数据列表</span>
                        <el-button type="primary" @click='ad' icon='el-icon-plus'>添加</el-button>
                        <el-dialog title="新增产品类型" :visible.sync="dialogVisible" width="30%">
                            <el-form label-width='100px' ref="form" :model="form" :rules="rules">
                                <el-form-item label='产品分类：' prop='name'>
                                    <el-input placeholder='输入产品类型' id='inputs' v-model='form.name' maxlength='20'></el-input>
                                </el-form-item>
                            </el-form>
                            <span slot="footer" class="dialog-footer">
                                <el-button @click="dialogVisible = false">取 消</el-button>
                                <el-button type="primary" @click="confirm('form')">确 定</el-button>
                            </span>
                        </el-dialog>
                    </div>
                    <div>
                        <el-table :data="product_list.slice((currentPage-1)*pagesize,currentPage*pagesize)" style="width: 100%" :header-cell-style="rowClass" :cell-style="cellStyle" border @row-click='headll'>
                            <el-table-column prop="id" label="序号" width="100">
                            </el-table-column>
                            <el-table-column prop="typeName" label="产品类型">
                            </el-table-column>
                            <el-table-column prop="realName" label="创建人" width='100'>
                            </el-table-column>
                            <el-table-column prop="createTime" label="创建时间" width='200'>
                            </el-table-column>
                            <el-table-column label="操作" width='150'>
                                <template scope='scope'>
                                    <span style='cursor:pointer;color:#0079FE;' @click.stop='del(scope.$index,scope.row.id)'><i class="el-icon-delete"></i> 删除</span>
                                </template>
                            </el-table-column>
                        </el-table>
                        <el-row>
                            <div class="pages">
                                <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="pagesize" layout="prev, pager, next, jumper" :total="total">
                                </el-pagination>
                            </div>
                        </el-row>
                    </div>
                    <el-dialog title="修改产品类型" :visible.sync="dialogVisible2" width="30%">
                        <el-form label-width='100px' ref="form" :model="form" :rules="rules">
                            <el-form-item label='产品分类：' prop='name'>
                                <el-input placeholder='输入产品类型' id='inputs' v-model='form.name' maxlength='20'></el-input>
                            </el-form-item>
                        </el-form>
                        <span slot="footer" class="dialog-footer">
                            <el-button @click="dialogVisible2 = false">取 消</el-button>
                            <el-button type="primary" @click="confirm2('form')">确 定</el-button>
                        </span>
                    </el-dialog>
                </div>
            </el-col>
        </el-row>
    </div>
</template>
<script>
import headers from '../../components/header.vue'
export default {
    components: {
        headers
    },
    data() {
        return {
            title: '产品类型',
            currentPage: 1,
            pagesize: 20,
            total: 1,
            product_list: [],
            dialogVisible: false,
            dialogVisible2: false,
            name: '',
            p_id: '',
            form: {
                name: ''
            },
            rules: {
                name: [{
                    required: true,
                    message: '请输入产品类型',
                    trigger: 'blur'
                }, {
                    min: 0,
                    max: 20,
                    message: '最大为20 个字符'
                }]
            }
        }
    },
    mounted() {
        this.all()
    },
    methods: {
        headll(row, event, column) {
            console.log(row, event, column)
            this.dialogVisible2 = true
            this.p_id = row.id
            this.form.name = row.typeName
        },
        /*打开添加弹框*/
        ad() {
            this.dialogVisible = true
            setTimeout(() => {
                let inputelement = document.getElementById('inputs')
                inputelement.focus()
            }, 1)
        },
        /*所有产品类型*/
        all() {
            this.$axios.post('/tomlive/productType/selectAllProductType').then((res) => {
                console.log(res)
                this.product_list = res.data.info.reverse()
                this.total = this.product_list.length
            })
        },
        rowClass({ row, rowIndex }) {
            return "text-align:center;background:#f5f5f5;color:#666"
        },
        cellStyle({ row, column, rowIndex, columnIndex }) {
            return "text-align:center;"
        },
        handleSizeChange(val) {
            // console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            // console.log(`当前页: ${val}`);
        },
        /*添加产品类型*/
        confirm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    var id = window.sessionStorage.getItem('userId')
                    this.$axios.post('/tomlive/productType/insertSelectProductType', this.$qs.stringify({
                        typeName: this.form.name,
                        userId: id
                    })).then((res) => {
                        this.all()
                        this.dialogVisible = false
                        this.form.name = ''
                    })
                } else {
                    console.log('error submit!!');
                    return false;
                }
            })
        },
        /*修改产品类型*/
        confirm2(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    var id = window.sessionStorage.getItem('userId')
                    this.$axios.post('/tomlive/productType/updateByPrimaryKeySelective', this.$qs.stringify({
                        typeName: this.form.name,
                        id: this.p_id
                    })).then((res) => {
                        this.all()
                        this.dialogVisible2 = false
                        this.form.name = ''
                    })
                } else {
                    console.log('error submit!!');
                    return false;
                }
            })
        },
        /*删除产品类型*/
        del(index, id) {
            this.$confirm('是否删除?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then((action) => {
                if (action === 'confirm') {
                    this.$axios.post('/tomlive/productType/deleteProductTypeByPrimaryKey', this.$qs.stringify({
                        id: id
                    })).then((res) => {
                        console.log(res)
                        this.all()
                        this.dialogVisible = false
                    })
                }
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消'
                });
            })
        }
    }
}
</script>
<style scoped>
.data_list {
    width: 100%;
    min-height: 800px;
    background: #fff;
    border: 1px solid #e9e9e9;
    box-sizing: border-box;
    margin: 20px 0 0 0;
    padding: 0 20px;

}

.data_title {
    width: 100%;
    height: 60px;
    font-size: 14px;
    color: #666;
    line-height: 60px;
    font-weight: bold;
}

.pages {
    margin: 20px 0 20px 0;
    float: right;
}

.data_title>button {
    float: right;
    margin: 10px 0;
}
</style>
<style>
.el-dialog__header {
    width: 100%;
    height: 50px;
    background: #f5f5f5;
    padding: 0;
}

.el-dialog__header>span {
    margin-left: 20px;
    font-size: 16px;
}

.el-dialog__title {
    line-height: 50px;
}
</style>